"use client";

import React, { useEffect, useState } from "react";
import { useParams, useRouter } from "next/navigation";
import VideoPlayer from "@/components/VideoPlayer";
import LivePoll from "@/components/LivePoll";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";

interface SoccerEventDetail {
  id: string;
  teamA: string;
  teamB: string;
  videoSrc: string;
  league: string;
  status: string;
  startTime: string;
}

export default function GameDetailPage() {
  const params = useParams();
  const router = useRouter();
  const id = params.id as string;
  
  const [eventDetail, setEventDetail] = useState<SoccerEventDetail | null>(null);
  const [error, setError] = useState<string | null>(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    if (!id) return;
    
    async function fetchEventDetail() {
      try {
        const res = await fetch(`/api/events?id=${id}`);
        if (!res.ok) {
          if (res.status === 404) {
            throw new Error("Game not found");
          }
          throw new Error("Failed to load game details.");
        }
        const data = await res.json();
        setEventDetail(data);
        setError(null);
      } catch (err: any) {
        setError(err.message);
      } finally {
        setLoading(false);
      }
    }
    
    fetchEventDetail();
  }, [id]);

  const formatTime = (timeString: string) => {
    const date = new Date(timeString);
    return date.toLocaleString();
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <div className="animate-spin rounded-full h-8 w-8 border-b-2 border-primary mx-auto mb-4"></div>
          <p className="text-muted-foreground">Loading match details...</p>
        </div>
      </div>
    );
  }

  if (error) {
    return (
      <div className="text-center py-12">
        <div className="bg-red-50 border border-red-200 rounded-lg p-6 max-w-md mx-auto">
          <h2 className="text-lg font-semibold text-red-800 mb-2">Error</h2>
          <p className="text-red-600 mb-4">{error}</p>
          <div className="space-x-2">
            <Button onClick={() => router.push("/")} variant="outline">
              Back to Home
            </Button>
            <Button onClick={() => window.location.reload()}>
              Retry
            </Button>
          </div>
        </div>
      </div>
    );
  }

  if (!eventDetail) {
    return (
      <div className="text-center py-12">
        <h2 className="text-xl font-semibold mb-2">Match Not Found</h2>
        <p className="text-muted-foreground mb-4">The requested match could not be found.</p>
        <Button onClick={() => router.push("/")}>
          Back to Home
        </Button>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Navigation */}
      <div className="flex items-center justify-between">
        <Button 
          onClick={() => router.push("/")} 
          variant="outline"
          className="mb-4"
        >
          ← Back to Matches
        </Button>
        <Badge variant={eventDetail.status === "LIVE" ? "default" : "secondary"}>
          {eventDetail.status}
        </Badge>
      </div>

      {/* Match Header */}
      <div className="text-center space-y-2">
        <h1 className="text-3xl font-bold">
          {eventDetail.teamA} vs {eventDetail.teamB}
        </h1>
        <div className="flex items-center justify-center space-x-4 text-muted-foreground">
          <span>{eventDetail.league}</span>
          <span>•</span>
          <span>{formatTime(eventDetail.startTime)}</span>
        </div>
      </div>

      {/* Video Player */}
      <div className="w-full">
        <VideoPlayer videoSrc={eventDetail.videoSrc} />
      </div>

      {/* Live Poll */}
      <div className="max-w-2xl mx-auto">
        <LivePoll 
          eventId={eventDetail.id} 
          options={[eventDetail.teamA, eventDetail.teamB]} 
        />
      </div>

      {/* Match Info */}
      <div className="bg-muted/50 rounded-lg p-6 text-center">
        <h3 className="font-semibold mb-2">About This Match</h3>
        <p className="text-muted-foreground text-sm">
          Watch the live stream above and place your bet on which team you think will win. 
          Vote counts are updated in real-time and you can see how other viewers are betting.
        </p>
      </div>
    </div>
  );
}
